.works{
    background: lightgreen;
}
.box_works{
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    width: 80%;
    border: 1px solid;
    flex-wrap: wrap;
    height: 110vh;
}
.face_title{
    display: flex;
    flex-direction: column;
    width: 280px;
    align-items: center;
    justify-content: center;
    margin: auto;
}
.box_works img{
    margin: auto;
}
